<template>
    <div class="addblog">
        <h3>添加博客</h3>
        <form v-on:submit.prevent="onsubmitd">
            <div class="form-group">
                <label >博客标题</label>
                <input type="text" class="form-control"  placeholder="title" v-model="blog.title">
            </div>
            <div class="form-group">
                <label>摘要</label>
                <input type="text" class="form-control" placeholder="summary" v-model="blog.summary">
            </div>
            <div class="form-group">
                <label>内容</label>
                <textarea class="form-control" placeholder="article" v-model="blog.article"></textarea>
            </div>

            <div class="checkbox">
                <label><input type="checkbox" value="daily" v-model="blog.categories">日志</label>

                <label><input type="checkbox" value="essay" v-model="blog.categories">随笔</label>

            </div>
            <button type="submit" class="btn btn-default">添加</button>
        </form>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        name: "addblog",
        data() {
            return {
                blog: {
                    title: '',
                    summary: '',
                    article: '',
                    categories:[]
                }

            }
        },
        methods: {
            ...mapActions(['addblog']),
            onsubmitd() {
                // axios.post('https://vuedemo-30ec9.firebaseio.com/posts.json',
                //     this.blog).then(data => {
                //     console.log(data)
                // })
                this.addblog(this.blog);
                this.blog={
                    title: '',
                    summary: '',
                    article: '',
                    categories:[]
                };
            }
        }
    }
</script>

<style scoped>
textarea{
    height: 200px;
    resize: none;
}
    .checkbox label{
        margin-right: 10px;
    }

</style>
